import { AbstractFilter } from './abstract_filter'

export class Term extends AbstractFilter {

    protected name: string = 'term';
    protected field: string;
    protected data: any = {};

    constructor(field: string, value: any) {
        super();
        this.field = field;
        this.set('value', value);
    }

    public static make(field: string, value: any): Term {
        return new Term(field, value);
    }

    public boost(value: number = 1): this {
        return this.set('boost', value);
    }

    protected set(option: string, value: any): this {
        this.data[option] = value;
        return this;
    }

    public toJSON() {

        let obj: any = {};
        obj[this.name] = {};
        obj[this.name][this.field] = this.data;

        return obj;
    }
}